@import-normalize; /* bring in normalize.css styles */

body {
  margin: 0;
  padding: 0;
  font-family: 'Rubik', sans-serif;
}

* {
  box-sizing: border-box;
}

:root {
  --fw-light: 300;
  --fw-regular: 400;
  --fw-medium-italic: 500;
  --fw-bold: 700;
}

body {
  font-family: 'Rubik', sans-serif;
  background-color: var(--colors-bg);
  margin: 0;
  padding: 0;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  padding: 0;
  margin: 0;
}

body {
  --colors-text: #333333;
  --colors-bg: #ff8c00;
  --colors-section: #f2ff00;
  --colors-input: #0084ff;
  --colors-btn: #4888a1;
}

.container {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 20px;
}

